<template>
    <!--商品详情页-->
    <transition name="move">
    <div class="food" v-show="showFlag" ref="foods">
        <div class="food-content">
          <div class="image-header">
            <img :src="food.image" alt="">
            <div class="back" @click="hide">
              <i class="icon-arrow_lift"></i>
            </div>
          </div>
          <div class="content">
            <h1 class="title">{{food.name}}</h1>
            <div class="detail">
              <span class="sell-count">月售{{food.sellCount}}份</span>
              <span class="rating">好评率{{food.rating}}%</span>
            </div>
            <div class="price">
              <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
            </div>
            <div class="cartcontrol-wrapper">
              <cartcontrol :food="food"></cartcontrol>
            </div>
            <transition name="fade">
              <div class="buy" @click.stop.prevent="addFirst" v-show="!food.count || food.count <= 0">加入购物车</div>
            </transition>
          </div>
          <split v-show="food.info"></split>
          <div class="info" v-show="food.info">
            <h1 class="title">商品信息</h1>
            <p class="text">{{food.info}}</p>
          </div>
          <split></split>
          <div class="rating">
            <h1 class="title">商品评价</h1>
            <ratingselect :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
            <div class="rating-wrapper">
              <ul v-show="food.ratings && food.ratings.length">
                <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="border-bottom-1px rating-item">
                  <div class="user">
                    <span class="name">{{rating.username}}</span>
                    <img class="avatar" width="12" height="12" :src="rating.avatar">
                  </div>
                  <div class="time">{{rating.rateTime | formatDate}}</div>
                  <p class="text">
                    <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                  </p>
                </li>
              </ul>
              <div class="no-rating" v-show="!food.ratings || !food.ratings.length">
                暂无评价
              </div>
            </div>
          </div>
        </div>
    </div>
    </transition>
</template>

<script type="text/ecmascript-6">
  // import 引入时，加{} 与不加{}的区别：当文件中写的是export default {}时，不需要加{} ,当文件中写的是 export function() {}/ export 变量;时，需要加{},而且可以有多个function 或者 变量。
  import BScroll from 'better-scroll';
  import Vue from 'vue'
  import {bus} from '../../common/js/bus.js'
  import {formatDate} from '../../common/js/date'
  import cartcontrol from '../cartcontrol/cartcontrol.vue'
  import split from '../split/split.vue'
  import ratingselect from '../ratingselect/ratingselect.vue'

  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
  export default {
    name:'food',
    props:{
      food:{
        type: Object
      }
    },
    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date,'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      'cartcontrol': cartcontrol,
      'split': split,
      'ratingselect': ratingselect
    },
    data() {
      return {
        scroll:null,
        showFlag: false,
        // 评价组件需要传递的参数
        onlyContent: false,// 控制 只看有内容的评价
        selectType: ALL,// 评价类型
        desc: { // 评价类型的描述
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      }
    },
    methods:{
      show() {//让商品详情页food显示
        this.showFlag = true;
        this.$nextTick(() => {
          if(!this.scroll) {
            this.scroll = new BScroll(this.$refs.foods,{
              click:true
            });
          } else {
            this.scroll.refresh();
          }
        })
      },
      hide() {
        this.showFlag = false;
      },
      addFirst() {
        if (!event._constructed) {
          return;
        }
        Vue.set(this.food,'count',1);
        bus.$emit('cart.add',event.target); // 触发自定义事件
      },
      needShow(type,text) { // type:评价类型：全部、满意、吐槽，text:只看有内容的评价
        if(this.onlyContent && !text) {
          return false;
        };
        if(this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
    },
    created () {
      bus.$on('ratingtype.select', (type) => { //监听子组件ratingselect.vue中是否点击了 评价类型
        console.log('评价类型',type);
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll?this.scroll.refresh():0;
        });
      });
      bus.$on('content.toggle',(onlyContent) => {
        console.log('只看又能容的评价',onlyContent);
        this.onlyContent = onlyContent;
        this.$nextTick(() => {
          this.scroll?this.scroll.refresh():0;
        });
      })
    }
  }
</script>

<style lang="scss">

  .food {
    position: fixed;
    left:0;
    top:0;
    bottom:48px;
    z-index: 30;
    width:100%;
    background: #fff;
    transition: all 0.5s linear;
    transform: translate3d(0, 0, 0);
    &.move-enter-active { /*进入时运动*/
      /*transition:all .5s;*/
    }
    &.move-leave-active { /*离开时运动*/
      /*transition:all .5s;*/
    }
    &.move-enter, &.move-leave-to
      /* .slide-fade-leave-active for below version 2.1.8 */ { /*离开*/
      transform: translate3d(100%, 0, 0);
    }
    .image-header {
      position:relative;
      width:100%;
      height:0;
      padding-top:100%;
      img {
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
      }
      .back {
        position:absolute;
        top:10px;
        left:0;
        .icon-arrow_lift {
          display: block;
          padding: 10px;
          font-size: 20px;
          color: #fff;
        }
      }
    }
    .content {
      position: relative;
      padding: 18px;
      .title {
        line-height: 14px;
        margin-bottom: 8px;
        font-size: 14px;
        font-weight: 700;
        color: rgb(7, 17, 27);
      }
      .detail {
        margin-bottom: 18px;
        line-height: 10px;
        height: 10px;
        font-size: 0;
        .sell-count, .rating {
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
        .sell-count {
          margin-right: 12px;
        }
      }
      .price {
        font-weight: 700;
        line-height: 24px;
        .now {
          margin-right: 8px;
          font-size: 14px;
          color: rgb(240, 20, 20);
        }
        .old {
          text-decoration: line-through;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
      }
      .cartcontrol-wrapper {
        position: absolute;
        right: 12px;
        bottom: 12px;
      }
      .buy {
        position: absolute;
        right: 18px;
        bottom: 18px;
        z-index: 10;
        height: 24px;
        line-height: 24px;
        padding: 0 12px;
        box-sizing: border-box;
        border-radius: 12px;
        font-size: 10px;
        color: #fff;
        background: rgb(0, 160, 220);
        transition: all 0.2s;
        opacity: 1;
        &.fade-enter, &.fade-leave-to {
          opacity: 0;
        }
      }
    }
    .info {
      padding: 18px;
      .title {
        line-height: 14px;
        margin-bottom: 6px;
        font-size: 14px;
        color: rgb(7, 17, 27);
      }
      .text {
        line-height: 24px;
        padding: 0 8px;
        font-size: 12px;
        color: rgb(77, 85, 93);
      }
    }
    .rating {
      padding-top: 18px;
      .title {
        line-height: 14px;
        margin-left: 18px;
        font-size: 14px;
        color: rgb(7, 17, 27);
      }
      .rating-wrapper {
        padding: 0 18px;
        .rating-item {
          position: relative;
          padding: 16px 0;
          .user {
            position: absolute;
            right: 0;
            top: 16px;
            line-height: 12px;
            font-size: 0;
            .name {
              display: inline-block;
              margin-right: 6px;
              vertical-align: top;
              font-size: 10px;
              color: rgb(147, 153, 159);
            }
            .avatar{
              border-radius: 50%;
            }
          }
          .time {
            margin-bottom: 6px;
            line-height: 12px;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
          .text {
            line-height: 16px;
            font-size: 12px;
            color: rgb(7, 17, 27);
            .icon-thumb_up, .icon-thumb_down {
              margin-right: 4px;
              line-height: 16px;
              font-size: 12px;
            }
            .icon-thumb_up {
              color: rgb(0, 160, 220);
            }
            .icon-thumb_down {
              color: rgb(147, 153, 159);
            }
          }
        }
        .no-rating {
          padding: 16px 0;
          font-size: 12px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }
</style>



