<template>
    <!--上下楼层之间的距离及边框-->
    <div class="split"></div>
</template>

<script type="text/ecmascript-6">
  export default {
    name:'split'
  }
</script>

<style lang="scss">
  .split {
    width: 100%;
    height: 16px;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    background: #f3f5f7;
  }
</style>



